@import url('https://fonts.googleapis.com/css2?family=Space+Grotesk:wght@400;500;600;700&family=Inter:wght@400;500;600&display=swap');

@tailwind base;
@tailwind components;
@tailwind utilities;

@layer base {
    :root {
        /* SNR Automations Brand Colors - Black & Yellow Theme */
        --background: 0 0% 5%;
        --foreground: 45 100% 96%;
        
        /* Primary: Golden Yellow - The signature SNR yellow */
        --primary: 47 100% 52%;
        --primary-foreground: 0 0% 5%;
        --primary-glow: 47 100% 65%;
        
        /* Secondary: Deep Black with slight warmth */
        --secondary: 0 0% 8%;
        --secondary-foreground: 47 100% 52%;
        
        /* Accent: Bright gold for highlights */
        --accent: 45 100% 50%;
        --accent-foreground: 0 0% 5%;
        
        /* Muted: Dark gray for subtle elements */
        --muted: 0 0% 12%;
        --muted-foreground: 0 0% 65%;
        
        /* Card: Slightly lighter than background */
        --card: 0 0% 8%;
        --card-foreground: 45 100% 96%;
        
        /* Popover */
        --popover: 0 0% 8%;
        --popover-foreground: 45 100% 96%;
        
        /* Border: Subtle yellow tint */
        --border: 45 30% 20%;
        --input: 45 30% 20%;
        --ring: 47 100% 52%;
        
        /* Destructive */
        --destructive: 0 84% 60%;
        --destructive-foreground: 0 0% 98%;
        
        /* Success */
        --success: 142 76% 36%;
        --success-foreground: 0 0% 98%;
        
        /* Custom gradients */
        --gradient-primary: linear-gradient(135deg, hsl(47 100% 52%), hsl(45 100% 60%));
        --gradient-hero: linear-gradient(180deg, hsl(0 0% 5%) 0%, hsl(45 20% 8%) 50%, hsl(0 0% 5%) 100%);
        --gradient-card: linear-gradient(135deg, hsl(0 0% 8%), hsl(45 10% 10%));
        
        /* Shadows with yellow glow */
        --shadow-glow: 0 0 40px hsla(47, 100%, 52%, 0.2);
        --shadow-strong: 0 10px 40px hsla(47, 100%, 52%, 0.3);
        --shadow-card: 0 4px 20px hsla(0, 0%, 0%, 0.5);
        
        /* Animations */
        --transition-smooth: 0.3s cubic-bezier(0.4, 0, 0.2, 1);
        --transition-bounce: 0.4s cubic-bezier(0.68, -0.55, 0.265, 1.55);
        
        --radius: 0.5rem;
    }
}

@layer base {
    * {
        @apply border-border;
    }
    
    body {
        @apply bg-background text-foreground;
        font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', sans-serif;
        -webkit-font-smoothing: antialiased;
        -moz-osx-font-smoothing: grayscale;
    }
    
    h1, h2, h3, h4, h5, h6 {
        font-family: 'Space Grotesk', sans-serif;
        font-weight: 600;
    }
    
    /* Smooth scrolling */
    html {
        scroll-behavior: smooth;
    }
    
    /* Selection color */
    ::selection {
        background: hsl(var(--primary));
        color: hsl(var(--primary-foreground));
    }
}

@layer utilities {
    /* Text gradient utilities */
    .text-gradient-primary {
        background: var(--gradient-primary);
        -webkit-background-clip: text;
        background-clip: text;
        -webkit-text-fill-color: transparent;
    }
    
    /* Glow effects */
    .glow-primary {
        box-shadow: var(--shadow-glow);
    }
    
    .glow-strong {
        box-shadow: var(--shadow-strong);
    }
    
    /* Hover glow animation */
    .hover-glow {
        transition: box-shadow var(--transition-smooth);
    }
    
    .hover-glow:hover {
        box-shadow: var(--shadow-glow);
    }
    
    /* Card with gradient background */
    .card-gradient {
        background: var(--gradient-card);
    }
    
    /* Slow spin animation for logo border */
    @keyframes spin-slow {
        from {
            transform: rotate(0deg);
        }
        to {
            transform: rotate(360deg);
        }
    }
    
    .animate-spin-slow {
        animation: spin-slow 8s linear infinite;
    }
}

@layer base {
    [data-debug-wrapper="true"] {
        display: contents !important;
    }

    [data-debug-wrapper="true"] > * {
        margin-left: inherit;
        margin-right: inherit;
        margin-top: inherit;
        margin-bottom: inherit;
        padding-left: inherit;
        padding-right: inherit;
        padding-top: inherit;
        padding-bottom: inherit;
        column-gap: inherit;
        row-gap: inherit;
        gap: inherit;
        border-left-width: inherit;
        border-right-width: inherit;
        border-top-width: inherit;
        border-bottom-width: inherit;
        border-left-style: inherit;
        border-right-style: inherit;
        border-top-style: inherit;
        border-bottom-style: inherit;
        border-left-color: inherit;
        border-right-color: inherit;
        border-top-color: inherit;
        border-bottom-color: inherit;
    }
}